
function DestinationCard(props) {
    return (
        <div class="flex items-center rounded-lg bg-white shadow-lg overflow-hidden">
            <img className="h-32 w-32 flex-shrink-0" src={props.destination.imageUrl} alt={props.destination.imageAlt} />
            <div className="px-6 py-4">
                <h3 className="text-lg font-semibold text-gray-800">{props.destination.city}</h3>
                <p className="text-gray-600">${props.destination.averagePrice} / night average</p>
                <div className="mt-4">
                    <a href="#" className="text-indigo-500 hover:text-indigo-400 font-semibold text-sm">Explore {props.destination.propertyCount} properties</a>
                </div>
            </div>
        </div>
    )
}
export default DestinationCard;